<template>
  <div>
    <div class="movie-top">
      <van-nav-bar
        title="电影详情"
        left-text="返回"
        left-arrow
        @click-left="fanhuei()"
      />
    </div>

    <div class="movie-src">
      <div class="movie-poster-ba" v-if="isplay">
        <img :src="movieData.images.large" alt="">
        <div class="ba-overlay">
          <van-icon v-if="movieData.trailer_urls.length != 0" class="ba-overlay-play" @click="BaPlay()" name="play-circle-o" />
        </div>
      </div>
      <van-swipe :autoplay="0" @change="onChange" indicator-color="white">
        <van-swipe-item v-for="(item, index) in movieData.trailer_urls" :key="index">
          <video ref="moviePlay" style="width: 100vw;" :src="isplay ? '' : item"  controls="controls"></video>
        </van-swipe-item>
      </van-swipe>

    </div>

    <div class="movie-xinxi">

      <div class="xinxi-poster-top clearfix">
        <div class="xinxi-img fl">
          <img class="auto-img" :src="movieData.images.large" alt="">
        </div>
        <div class="xinxi-jianjie fl">
          <div class="xinxi-title one-text">{{movieData.title}}</div>
          <div class="xinxi-type one-text">{{movieData.genres.join('/')}}</div>
          <div class="xinxi-date"><van-icon name="underway-o" /> {{movieData.durations[0]}}</div>
          <div class="xinxi-shouc"><van-icon name="like-o" /> {{movieData.comments_count}}</div>
          <div class="xinxi-fen clearfix">
            <div>
              <van-rate
                v-model="movieData.rating.aug"
                :size="25"
                color="#ff3f7e"
                void-icon="star"
                void-color="#eee"
                :readonly="true"
                :allow-half="true"
              />
             </div>
            <!-- <div>{{movieData.rating.average}}</div> -->
          </div>

        </div>
        <div class="my-shouc fr" :class="{'activate' : myActivate}">
          <van-icon @click="myac()" name="like" />
        </div>
        <div class="Ticket_Purchase">
          <van-button icon="shopping-cart-o" size="small" type="primary">立即购票</van-button>
        </div>
      </div>

      <div class="summary">
        <h5>电影简介</h5>
        <p :style="{'display': isOverflow ? '-webkit-box' : 'block'}">{{movieData.summary}}</p>

        <van-icon class="down" @click="isdisplay()"  :name="isOverflow ? 'arrow-down' : 'arrow-up'" />
      </div>

    </div>

    <div class="qita-tabs">
      <van-tabs :v-model="1" animated>

        <van-tab class="qita-yingren" title="相关影人" >
          <ul class="classify">
            <li v-for="(item, index) in movieData.casts" :key="index">
              <img class="auto-img" :src="item.avatars.small" :alt="item.name">
              <p>{{item.name}}</p>
            </li>
          </ul>
        </van-tab>

        <van-tab title="相关花絮" >
            <van-swipe :autoplay="0" indicator-color="white">
              <van-swipe-item v-for="(item, index) in movieData.bloopers" :key="index">
                <video style="width: 100%; margin-top: 5px;" :src="item.resource_url"  controls="controls"></video>
              </van-swipe-item>
            </van-swipe>
        </van-tab>

        <van-tab class="pinlun" title="相关评论" >
          <!-- popular_reviews -->
          <ul>
            <li v-for="(item, index) in movieData.popular_reviews" :key="index">
              <div class="swie-user clearfix">
                <div class="swie-user-img fl">
                  <img class="auto-img" :src="item.author.avatar" alt="">
                </div>
                <div  class="swie-user-left fl">
                  <div class="swie-user-name">{{item.author.name}}</div>
                  <div class="swie-user-evaluate">
                    <van-rate
                      v-model="item.rating.value"
                      allow-half
                      void-icon="star"
                      void-color="#eee"
                      :readonly="true"
                      size="14px"
                    />
                  </div>
                </div>
              </div>

              <div class="swie-user-con">
                {{item.summary}}
              </div>

            </li>
          </ul>
        </van-tab>

      </van-tabs>
    </div>

  </div>
</template>

<script>
  import { Dialog } from 'vant';
  export default {
    name: 'movieDetail',

    data() {
      return {
        movieData: {
          'trailer_urls': [],
          images:'',
          genres: [],
          durations: [],
          rating:{},
        },

        isplay: true,

        myActivate: false,

        isOverflow: true,

        movieId: null,
      };
    },

    methods:{
      myac(){
        let UserName = huoquzhi('LandUser');
        if(!UserName){
          Dialog.confirm({
            title: '未登录提醒',
            message: '请登录后再进行收藏,是否跳转登录页?'
          }).then(() => {
            this.$router.push({name: 'Land'});
          }).catch(() => {
            return;
          });
          return;
        }
        this.myActivate = !this.myActivate;
        let len = this.movieData.comments_count
        len = this.myActivate ? ++len : --len;
        this.movieData.comments_count = len;


        let AllMdata = getjsonloca('Collection', {});
        let Mdata = AllMdata[UserName];
        Mdata = Mdata == undefined ? {} : Mdata;

        if(!this.myActivate){
          delete Mdata[this.movieId];
        }else{
          Mdata[this.movieId] = this.movieData;
        }
        AllMdata[UserName] = Mdata;
        insertjsonloca('Collection', AllMdata);
        // console.log(Mdata);
        this.$toast({
          message: this.myActivate ? '收藏成功' : '取消成功',
          icon: 'like-o',
        });

      },
      fanhuei(){
        this.$router.go(-1);

      },

      BaPlay() {
        this.isplay = false;
        // console.log(this.$refs.moviePlay[0])
        this.$refs.moviePlay[0].oncanplay = function() {
          this.play();
        }
      },

      onChange(index) {
        this.$refs.moviePlay.forEach(v => {
          v.pause();
        })
        this.$refs.moviePlay[index].play();
      },

      isdisplay() {
        this.isOverflow = !this.isOverflow;
      }

    },

     activated() {
      // return;
      //截取路由参数
      this.isplay = true;
      this.isOverflow = true;
      let id = this.$route.params.id;
      this.movieId = id;
      let sfel = this;
      console.log('id ==> ', id);
      let Mdata = getjsonloca('Collection', {});
      let UserName = huoquzhi('LandUser');
      this.myActivate = Mdata[UserName] != undefined && Mdata[UserName][id] != undefined;

      // 显示提示框
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true
      });

      this.axios({
        method: 'GET',
        url: 'https://douban.uieee.com/v2/movie/subject/' + id
      }).then(result => {
        console.log(result);
        result.data.rating.aug = result.data.rating.average / 2;
        sfel.movieData = result.data;
        // 关闭提示框
        sfel.$toast.clear();
      })
    }

  }

</script>

<style scoped lang="less">
  .Ticket_Purchase{
    position: absolute;
    right: 20px;
    bottom: 10px;

  }
  .pinlun{

    .swie-user-con{
      background-color: #F2F2F2;
      border-radius: 15px;
      padding: 10px;
      line-height: 20px;
      text-indent: 14px;
    }
    .swie-user-left{
      padding: 10px;
      .swie-user-name{
        font-size: 14px;
        line-height: 18px;
      }
    }
    ul{
      padding: 5px;
      li{
        margin-bottom: 20px;
        .swie-user-img{
          margin-left: 10px;
          width: 1.2rem;
          border-radius: 50%;
          overflow: hidden;

        }
      }
    }
  }
  .qita-yingren{
    ul{
      white-space:nowrap;
      overflow-x: auto;
      text-align: center;
      >li{
        display: inline-block;
        width: 3rem;
        margin: .2rem;
        >img{
          border-radius: 10px;
        }
        >p{
          font-size: 16px;
          margin: 5px;
          color: #333;
        }
      }
    }
  }
  .summary{
    padding: .2rem;
    line-height: 18px;
    >p{
      ext-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      text-indent: 14px;
    }
    position: relative;
    >h5{
      margin: 5px;
      font-size: 16px;
    }
    >.down{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      font-size: 24px;
      color: #666;
    }
  }
  .my-shouc{
    font-size: 24px;
    padding: .2rem;
    color: #ddd;
  }
  .my-shouc.activate{
    color: #ff3f7e;
  }
  .xinxi-fen{
    font-size: 16px;
    line-height: 26px;
    >div{
      float: left;
      &:nth-child(2){
        margin-left: 14px;
        color: #E6A23C;
      }

    }
  }
  .xinxi-shouc{
    font-size: #666;
    margin: .2rem 0;
  }
  .xinxi-date{
    font-size: #666;
    margin: .2rem 0;
  }
  .xinxi-type{
    font-size: 16px;
    font-size: #666;
    margin: .2rem 0;
  }
  .xinxi-title{
    font-size: 0.533rem;
    margin: .2rem 0;
  }
  .xinxi-jianjie{
    padding: 0 .2rem;
    width: calc(~"50vw");
  }
  .xinxi-img{
    width: 2.5rem;
    border-radius: 10px;
    overflow: hidden;
  }
  .xinxi-poster-top{
    padding: .2rem;
    position: relative;
  }
  .movie-top{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
  }
  .movie-src{
    position: relative;
    .movie-poster-ba{
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 2;
      overflow: hidden;
      >img{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 100%;
      }
      >.ba-overlay{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0,0,0,.7);

        >.ba-overlay-play{
          position: absolute;
          color: #fff;
          font-size: 2rem;
          top: calc(~"50% - 1rem");
          left: calc(~"50% - 1rem");
        }
      }
    }
  }
</style>
